<template>
  <b-modal id="modal-complete-Contract" hide-header-close no-close-on-backdrop size="md">
    <template #modal-title>{{ title }} </template>
    <div class="form-group">
      <label for="">Tên công ty</label>
      <input type="text" class="form-control" v-model="Contract.company_name" disabled />
    </div>
    <div class="form-group">
      <label for="">Ngày hoàn hiện</label>
      <date-picker v-model="Contract.complete_date" :config="options"></date-picker>
    </div>
    <div class="form-group" v-if="type == 1">
      <label for="">Trạng thái thanh toán</label>
      <select class="form-select" v-model="payment_status">
        <option value="3">Đã nộp tiền</option>
        <option value="4">Đã nộp tiền và làm thanh toán</option>
      </select>
    </div>
    <template #modal-footer>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-complete-Contract')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
      <button @click="saveComplete" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
    </template>
  </b-modal>
</template>

<script>
import moment from "moment";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import axios from "axios";

export default {
  components: {
    datePicker,
  },
  data() {
    return {
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      Contract: [],
      type: 0,
      title: "Hoàn thiện hợp đồng",
      payment_status: 3,
    };
  },
  methods: {
    show(contract = null, type = 0) {
      this.title = type == 0 ? "Hoàn thiện hợp đồng" : "Thanh toán hợp đồng";
      this.Contract = contract;
      this.type = type;
    //  this.Contract.complete_date = moment().format("DD/MM/YYYY");
      this.Contract.complete_date = moment();
      this.$bvModal.show("modal-complete-Contract");
    },
    async saveComplete() {
     
      await axios
        .put(`/contracts/updatestatus`, {
          type: this.type,
          payment_status: this.payment_status,
          contract_id: this.Contract.contract_id,
          complete_date: moment(this.Contract.complete_date).format('YYYY-MM-DD HH:mm:ss'),
        })
        .then((repsonse) => {
          if (repsonse.data.message == "success") {
            this.$toast.success("Cập nhật dữ liệu thành công");
            this.$emit("updateCompleteContract", repsonse.data.data);
          }
        });
      this.$bvModal.hide("modal-complete-Contract");
    },
  },
};
</script>
