<template>
  <div id="debt-index">
    <h1 class="fw-bold title-page">Danh sách hợp đồng còn nợ</h1>
    <div class="row">
      <div class="col-3 mb-2">
        <div class="card">
          <div class="card-body">
            <h4 class="fw-bold">Tổng số nợ</h4>
            <h5 class="text-danger fw-bold">{{ debtTotal | formatamout }}đ</h5>
          </div>
        </div>
      </div>
    </div>

    <table class="table">
      <thead class="table-dark">
        <tr>
          <th class="rounded-start">#</th>
          <th width="35%">Tên công ty</th>
          <th>Mã số thuế</th>
          <th>Số tiền (VAT)</th>
          <th>Đầu mối</th>
          <th>Ngày HĐ</th>
          <th>Ngày xuất HĐ/HT</th>
          <th class="rounded-end"></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td></td>
          <td>
            <input
              v-model="searchArray.company_name"
              type="text"
              class="form-control"
              placeholder="Tìm tên công ty"
            />
          </td>
          <td>
            <input
              v-model="searchArray.company_taxcode"
              type="text"
              class="form-control"
              placeholder="Mã số thuế"
            />
          </td>
          <td>
            <input
              type="number"
              v-model="searchArray.contract_total"
              class="form-control"
              placeholder="Số tiền tổng"
              @input="filterArray"
            />
          </td>
          <td>
            <input
              type="text"
              v-model="searchArray.customer_name"
              class="form-control"
              placeholder="Đầu mối liên hệ"
            />
          </td>
          <td></td>
          <td></td>
        </tr>
        <tr v-for="(item, index) in contractFilltered" :key="item.contract_id">
          <td>
            {{ index + 1 }}
          </td>
          <td>
            <router-link
              :title="item.company_name"
              :to="{ name: 'contract-detail', params: { id: item.contract_id } }"
            >
              {{ item.company_name }}
            </router-link>
          </td>
          <td>{{ item.company_taxcode }}</td>
          <td>
            <strong class="text-danger">{{ item.contract_total | formatamout }}</strong>
          </td>
          <td>{{ item.customer_name }}</td>
          <td>{{ item.contract_date | moment("DD/MM/YYYY") }}</td>
          <td>{{ item.complete_date | moment("DD/MM/YYYY") }}</td>
          <td>
            <button
              @click="completeContract(item, 1)"
              class="btn btn-primary-soft btn-sm"
              title="Thanh toán"
            >
              <i class="fa-solid fa-circle-dollar"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <not-found v-if="contractFilltered.length == 0"></not-found>
    <completeContract
      ref="completeContract"
      @updateCompleteContract="updateCompleteContract"
    ></completeContract>
  </div>
  <!-- <DebtComponent @updat_debt="update_debt" :contracts="contracts"></DebtComponent> -->
</template>

<script>
import axios from "axios";
import NotFound from "@/extends/NotFound.vue";
import completeContract from "@/extends/ModalCompleteContract.vue";

export default {
  components: {
    NotFound,
    completeContract,
  },
  data() {
    return {
      contracts: [],
      keyword: "",
      searchArray: {
        company_name: "",
        contract_total: "",
        customer_name: "",
        company_taxcode: "",
      },
    };
  },
  computed: {
    debtTotal() {
      let debt = this.contractFilltered
        .filter((item) => item.contract_payment == 2)
        .reduce((sum, result) => {
          return sum + parseFloat(result.contract_total);
        }, 0);
      return debt;
    },

    contractFilltered() {
      let result = this.contracts.filter((item) => {
        return (
          item.contract_payment == 2 &&
          item.company_name
            .toLowerCase()
            .indexOf(this.searchArray.company_name.toLowerCase()) !== -1 &&
          item.contract_total.indexOf(this.searchArray.contract_total) !== -1 &&
          item.customer_name
            .toLowerCase()
            .indexOf(this.searchArray.customer_name.toLowerCase()) !== -1
        );
      });

      return result;
    },
  },
  created() {
    this.getContracts();
  },
  methods: {
    filterArray() {
      console.log(this.searchArray.contract_total);
      this.$emit("update_debt", this.searchArray.contract_total);
    },
    contractStatus(status) {
      return this.$econtract.contractStatus(status);
    },
    updateCompleteContract(value) {
      let result = this.contracts.filter((item) => {
        return item.contract_id == value.contract_id;
      })[0];
      result.contract_status = value.contract_status;
      result.contract_payment = value.contract_payment;
    },
    completeContract(contract, type) {
      this.$refs.completeContract.show(contract, type);
    },
    async getContracts() {
      this.$econtract.showLoading();
      await axios.get(`${API_URL}/debt`).then((response) => {
        this.contracts = response.data;
        this.$econtract.hideLoading();
      });
    },
  },
};
</script>
